<template>
	<div class="view" :style="{animation: !$loaded ? 'bottomElation 0.5s 1s both' : 'bottomElation 0.5s both'}">
		<h2 class="contact-title">Skontaktuj sie z nami!</h2>
		<iframe class="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2311.9395646375056!2d18.08398021578049!3d54.58743898025791!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46fdb8af86a5071b%3A0x4ddfccfb000db595!2sJagodowa%2017%2C%2084-242%20K%C4%99b%C5%82owo!5e0!3m2!1spl!2spl!4v1607187845546!5m2!1spl!2spl" width="1000" height="500" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
		<div class="info">
				<div class="address">
					<!-- <fa-icon class="home-icon" icon="home" /> -->
					<!-- <fa-icon class="car-icon" icon="car-side" /> -->
					<a href="https://goo.gl/maps/THdsmhM3zskfnZNA8" target="_blank" class="address-link">
						<div class="company-name"><span class="company-name--letter">C</span>ichy <span class="company-name--letter">D</span>worek</div>
						<div>ul. Jagodowa 17</div>
						<div>84-242 Kębłowo</div>
					</a>
					<br/>
					<a href="https://prod.ceidg.gov.pl/CEIDG/CEIDG.Public.UI/SearchDetails.aspx?Id=bcc6424f-2d34-4602-94bc-5bb5ad48faf6" target="_blank" class="address__link">
						<fa-icon class="address__link__icon" icon="id-card" />
						NIP 958-095-92-20
					</a>
					<a href="mailto:kamilalewko@wp.pl" target="_blank" class="address__link">
						<fa-icon class="address__link__icon" icon="envelope" />
						kamilalewko@wp.pl
					</a>
					<div class="social-media">
						<a href="https://www.facebook.com/cichydworek" target="_blank">
							<fa-icon class="social-media__icon" :icon="{ prefix: 'fab', iconName: 'facebook-square' }" />
						</a>
						<a href="https://www.messenger.com/t/100658321286481" target="_blank">
							<fa-icon class="social-media__icon" :icon="{ prefix: 'fab', iconName: 'facebook-messenger' }" />
						</a>
					</div>
				</div>
			<form class="form" @submit.prevent="sendEmail">
				<div class="input-div">
					<input
						id="name"
						class="input"
						type="text"
						placeholder=" "
						pattern="^.{3,}$"
						required
						v-model="name"
					/>
					<label for="name" class="label">Imię</label>
				</div>
				<div class="input-div">
					<input
						id="email"
						class="input"
						type="email"
						placeholder=" "
						pattern="^\S+@\S+\.\S+$"
						required
						v-model="email"
					/>
					<label for="email" class="label">Email</label>
				</div>
				<textarea
					class="content"
					required
					placeholder="Co chcesz nam przekazać?"
					v-model="content"
				/>
				<button class="submit" type="submit">
					Wyślij
					<fa-icon class="submit-icon" icon="envelope" />
				</button>
			</form>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Contact",
		data() {
			return {
				name: '',
				email: '',
				content: ''
			}
		},
		methods: {
			sendEmail() {
				this.$db.collection('emails').add({
					name: this.name,
					email: this.email,
					content: this.content
				})
				window.open(`mailto:kamilalewko@wp.pl?subject=${this.name}&body=Imię: ${this.name}%0AEmail: ${this.email}%0A%0A"${this.content.replace(/\n/g, '%0A')}"`);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.contact-title {
		position: relative;
		display: inline-block;
		margin: 30px 0 ;
		font-size: 30px;
		@include line;
		/* color: $secondary; */
		&::first-letter {
			color: $decorative;
			font-size: 40px;
		}
	}
	.map {
		display: block;
		margin: 20px auto;
		max-width: 100%;
		max-height: 60vh;
		border-radius: 10px;
		margin-bottom: 50px;
		background-color: $secondary;
	}
	.info {
		width: 100%;
		max-width: 1000px;
		margin: 0 auto;
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		@media (min-width: 950px) {
			justify-content: space-between;
			flex-direction: row;
		}
		.address {
			position: relative;
			width: 300px;
			font-size: 20px;
			font-weight: bold;
			text-align: left;
			color: $decorative;
			background-color: rgb($decorative, 0.1);
			border: 3px solid rgb($decorative, 0.2);
			padding: 20px;
			margin: 30px;
			border-radius: 20px 10px 20px 20px;
			.company-name {
				font-size: 27px;
				.company-name--letter {
					color: $decorative;
					font-size: 35px;
				}
			}
			.address__link {
				display: block;
				transition: all 0.2s ease;
				color: $decorative;
				@include hover {
					letter-spacing: 1px;
					.address__link__icon {
						transform: scale(1.2) rotate(-5deg);
					}
				}
				.address__link__icon {
					transition: all 0.2s ease;
					margin-right: 10px;
				}
			}
			.social-media {
				display: flex;
				justify-content: center;
				margin-top: 10px;
				.social-media__icon {
					color: $decorative;
					margin: 0 10px;
					transition: all 0.2s ease;
					@include hover {
						transform: scale(1.2);
					}
				}
			}
		}
		@mixin formItem {
			padding: 5px 10px;
			border: 2px solid rgb($decorative, 0.5);
			border-radius: 7px;
			outline: none;
			transition: all 0.2s ease;
			&:focus {
				border: 2px solid $decorative;
			}
		}
		.form {
			width: 600px;
			max-width: 100%;
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 10px;
			.input-div {
				position: relative;
				width: 100%;
				.input {
					width: 100%;
					background-color: transparent;
					
					&:focus,
					&:not(:placeholder-shown) {
						~ .label {
							top: -17px;
							left: 0px;
							transform: scale(0.8);
							z-index: 2;
						}
					}
					&:valid {
						~ .label::after {
							content: '\f00c';
							font-family: "FontAwesome";
							color: $decorative;
							margin-left: 5px;
						}
					}
					@include formItem();
				}
				.label {
					position: absolute;
					top: 50%;
					left: 15px;
					transform: translateY(-50%);
					z-index: -1;
					background-color: $bg;
					border-radius: 7px;
					padding: 0 5px;
					transition: all 0.2s ease;
					.icon {
						margin-right: 3px;
					}
				}
			}
			.content {
				@include formItem();
				grid-column: 1 / 3;
				height: 250px;
				resize: none;
				&:focus {
					&::placeholder {
						transition: all 0.3s ease;
						letter-spacing: 3px;
						opacity: 0;
					}
				}
			}
			.submit {
				@include formItem();
				grid-column: 1 / 3;
				text-transform: uppercase;
				letter-spacing: 1px;
				color: $logo;
				background-color: $bg;
				font-weight: bold;
				font-size: 18px;
				color: $decorative;
				cursor: pointer;
				.submit-icon {
					margin-left: 3px;
				}
				@include hover {
					background-color: rgb($decorative, 0.1);
				}
			}
		}
	}
</style>